<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="row">
            <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left">
                <multiselect  placeholder="Select Form" v-model="value"  label="name" track-by="value" :options="ListData" v-bind:multiple="true" v-bind:taggable="true"></multiselect>
            </div> 
            <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left">
                <div class="search-btm-btn background-0 text-left form-check-inline">
                    <a title="ClickHereToSearch" v-on:click="SearchUserLatLng()" href="javascript:;" class="btn btn-success mr-1"><i class="fa fa-search"></i></a>
                    <a title="ClickHereToReset" @click="Reset"  class="btn btn-danger"><i class="fa fa-refresh"></i></a>
                </div> 
            </div>
        </div>
        <MapComponent class="row" mapContainer="TechTracking" mapType="Google" :selectedUser="selectedUser" mapWidth="100%" mapHeight="780px" :config="config"></MapComponent>
    </section>
</template>
<script>
import DataServices from "@/services/DataService";
import MapComponent from '../../components/Common/MapComponent.vue';
import Loader from '../../components/Common/Loader.vue';
export default {
    components:{
        MapComponent,
        Loader
    },
    data(){
        return{
            isLoading:false,
            ListData:[],
            value: [],
            selectedUser:'',
            config:{
                isShowSearchBox:false,
                isShowCurrentLocation:false
            }
        }
    },
    created:function(){
        var vm=this;
        vm.GetDDLData("FSM_USERS");
    },
    methods:{
        GetDDLData:function(){
            debugger
            var vm=this;
            var selectedUser='';
            vm.isLoading=true;
            let appendingUrl = `moduleName=Ticketing&type=${'FSM_USERS'}`;
            DataServices.GetDDLData(appendingUrl).then(function (response) {
                response.data.DATA.forEach((item, index) => {
                    selectedUser += ',';
                    selectedUser += item.value;
                    vm.selectedUser=selectedUser;
                    vm.ListData.push({  
                        name: item.name,                      
                        value: item.value                                              
                    })
                })
                vm.isLoading=false;
            });
        },
        SearchUserLatLng:function(){
            var vm=this;
            vm.selectedUser=vm.value[0].value;
        },
        Reset:function(){
            debugger
            var vm=this;
            vm.value=[];
            vm.GetDDLData();
            vm.selectedUser='';
        }
    }
}
</script>
